.post-form-field {
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}

.post-form-field input,
.post-form-field textarea {
  font-weight: 400;
}

.post-form-field > textarea {
  font: inherit;
  min-height: 15rem;
  resize: none;
  overflow: hidden;
  border: 1px solid #cfd8dc;
  color: #546e7a;
  padding: 9px 13px;
  border-radius: 3px;
}

.post-form-field > label {
  font-size: 1.75rem;
}
