.profile-edit-container {
  margin: 15rem auto 5rem;
  padding: 0 3rem;
}

.profile-edit-card {
  padding: 3rem max(1.5rem, 6vw) 3.5rem;
  max-width: 110rem;
  border: 1px solid #d6d4d4;
  margin: auto;
  border-radius: 8px;
  margin-bottom: 5rem;
}

.profile-header {
  display: flex;
  justify-content: space-between;
  gap: 2.5rem;
  margin-bottom: 2.5rem;
  align-items: center;
  flex-wrap: wrap;
}

.profile-header h1 {
  font-size: 2.75rem;
  font-weight: 500;
}

.edit-btn button {
  cursor: pointer;
  border: 1px solid #414141;
  padding: 4px 20px;
  border-radius: 20px;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 500;
  background: transparent;
}

.edit-btn button:hover {
  color: #666666;
  border: 1px solid #666666;
}

.col {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  flex-wrap: wrap;
}

.type-1 {
  width: 45%;
  min-width: 25rem;
  margin-bottom: 0.25rem;
}

@media (max-width: 661px) {
  .type-1 {
    flex: 1;
    width: 100%;
  }
}

@media (max-width: 360px) {
  .type-1 {
    min-width: 20rem;
  }
}

.type-1 input,
.type-1 select {
  font-family: inherit;
  font-size: 1.25rem;
  padding: 2rem;
  padding: 8px 10px;
  color: #414141;
  font-weight: 400;
  width: 100%;
  border: 1px solid #cfd8dc;
  transition: 0.4s all;
  line-height: 1.4;
  border-radius: 3px;
}

.type-1 input:focus,
.type-1 select:focus {
  outline: none;
}

.type-1 ::placeholder {
  font-style: italic;
  font-size: 1.3rem;
  color: #a5a5a5;
}

.col-placeholder {
  text-transform: uppercase;
  margin-bottom: 0.5rem;
  font-size: 1.25rem;
  letter-spacing: 0.7px;
  font-weight: 500;
}
