.navbar {
  position: fixed;
  z-index: 100;
  top: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 15px 10px -15px #1111112b;
}

.nav-links {
  display: flex;
  justify-content: space-between;
  min-height: 6.5rem;
  padding: 0 3.5rem;
}

.course-topics {
  border-top: 1px solid #eeeeee;
}

.courses {
  display: flex;
  align-items: center;
  min-height: 4.8rem;
  padding: 0 3.5rem;
}

.nav-left,
.nav-right {
  display: flex;
  align-items: center;
}

.nav-item,
.course-item {
  margin: 0 1.25rem;
}

.login-btn {
  outline: 0;
  background-color: #273239;
  color: #fff !important;
  padding: 8px 27px;
  border-radius: 5px;
  font-size: 14px;
  border: 0;
}

.nav-item a {
  font-weight: 600;
  color: #121212;
}

.nav-logo a {
  display: flex;
  margin-top: 3px;
}

.nav-logo img {
  width: 10rem;
}

.course-item a {
  font-weight: 500;
  color: var(--clr-black);
  font-size: 11pt;
}

/* 
Dropdown 
*/

.user {
  position: relative;
  height: 100%;
  width: 3rem;
  cursor: pointer;
}

.user > svg {
  height: inherit;
  width: inherit;
  transition: width 0.4s;
}

.user > svg:hover {
  width: 2.6rem;
}

.dropdown {
  position: absolute;
  width: 200px;
  right: -35px;
  top: 100%;
  z-index: 100;
  color: var(--clr-black);
  font-weight: 400;
  box-shadow: 0 0 15px 0 rgb(0, 0, 0, 18%);
  background: #fff;
}

.dropdown-items {
  display: flex;
  padding: 1rem 2rem;
  gap: 2rem;
}

.dropdown-title {
  font-size: 1.5rem;
  font-weight: 500;
}

.dropdown-logo {
  display: flex;
  align-items: center;
  font-size: 2.2rem;
  color: #454545;
}

.dropdown-items:hover {
  background: #ececec;
}
